import React, { useEffect, useState } from "react";
import bibtexParse from "bibtex-parse-js";

export default function Publications() {
  const [entries, setEntries] = useState([]);

  useEffect(() => {
    fetch("/publications.bib")
      .then((res) => res.text())
      .then((bibtexText) => {
        const parsed = bibtexParse.toJSON(bibtexText);
        setEntries(parsed);
      });
  }, []);

  return (
    <section id="publications">
      <h2>Publications</h2>
      {entries.length === 0 && <p>Loading publications...</p>}
      {entries.map((e, i) => (
        <div key={i} className="publication">
          <b>{e.entryTags.title}</b><br />
          {e.entryTags.author}<br />
          <i>{e.entryTags.journal}, {e.entryTags.year}</i>
        </div>
      ))}
    </section>
  );
}